

import "./Product.css";
import "./productlist.css"
import { useStateValue } from "./StateProvider";
import "./DeskTopHomepageLayout.css"
import DeskTopHomepageLayoutProduct from "./DeskTopHomepageLayoutProduct"
import React, { Component } from "react";
import axios from "axios";
import Product from "./Product";
import { endpoint, endpointlocahost } from "../constants";
import { NavLink } from 'react-router-dom'

class PhoneHomePageProductList10 extends Component {





    state = {
        loading: false,
        error: null,
        data: []
    };
    componentDidMount() {
        this.setState({ loading: true });



        axios
            .get(`${endpoint}/PhoneHomePageProductListSlider10ListView/`)

           


            .then(res => {
                this.setState({ data: res.data.data, loading: false });
                // console.log('phn data ',res.data.data)




            })
            .catch(err => {
                this.setState({ error: err, loading: false });
            });
    }



    render() {
        const { data } = this.state
        console.log('phn data1 ', data)


        return (
            <>

                {data &&
                    data.map(v => {
                        return (

                            <div className='col-3' style={{ padding: '1px', }}>
                                <NavLink to={v.link}>
                                    <img src={`${endpointlocahost}/media/${v.image} `} style={{ borderRadius: '0px', padding: 0 }} class="img-thumbnail" alt="..." />
                                </NavLink>
                            </div>
                        );

                    })}
            </>
        );
    }
}
export default PhoneHomePageProductList10;
