    handleChange = e => {
        const { formData } = this.state;
        const updatedFormdata = {
            ...formData,
            [e.target.name]: e.target.value
        };
        console.log(updatedFormdata, 'kkkkkkkkkkkkkkkkkkkkkkkkkkkk')

        this.setState({
            formData: updatedFormdata
        });

        this.handleFetchChangeApply()
    };

    handleFetchItem = () => {
        const {
            match: { params }
        } = this.props;
        console.log(this.props)
        this.setState({ loading: true });
        axios
            // .get(OrderDetailURL(params.ordercancellerandreturnsID))
            .get(`http://127.0.0.1:8000/shop/ordercancelledandreturnsdetail/${params.ordercanclledstatuschangeID}/`)

            .then(res => {
                console.log('data1', res.data)
                this.setState({ formData: res.data, loading: false });
            })
            .catch(err => {
                this.setState({ error: err, loading: false });
                console.log(err)
            });
    };


    handleFetchChangeApply = () => {
        const { data, formData } = this.state;
     
        const {
            match: { params }
        } = this.props;

        
        axios
            .put(`http://127.0.0.1:8000/shop/ordercancelledandreturns/${params.ordercanclledstatuschangeID}/`, { ...formData})
            .then(res => {
                this.setState({ data: res.data, loading: false });
            })
            .catch(err => {
                this.setState({ error: err, loading: false });
            });

    };



